@charset "utf-8";
/* -------------------------------------------
reset
------------------------------------------- */
html{scroll-behavior:smooth;font-family:Meiryo,"Hiragino Kaku Gothic Pro",Arial,sans-serif;color:#333333;background:#ffffff;}body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td{padding:0;margin:0}table{border-spacing:0;border-collapse:collapse}fieldset,img{border:0}ol,ul{list-style:none}caption,th{text-align:left}h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal}sup{vertical-align:text-top}sub{vertical-align:text-bottom}q::before{content:open-quote}q::after{content:close-quote}abbr,acronym{font-variant:normal;border:0}input,textarea{font-family:inherit;font-size:inherit;font-weight:inherit}select,button{font:inherit}input,select,textarea,button{outline:none}*,*::before,*::after{box-sizing:border-box}img{max-width:100%;height:auto}body{color:#333333;-webkit-text-size-adjust:100%}legend{color:#333333}a{color:inherit;text-decoration:none;}

/* ------------------------------------------- */
h1{
  padding: 16px 8px;
  font-size: 24px;
  font-weight: bold;
  color: #ffffff;
  text-align: center;
  background: #333333;
}
#msg-area{
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #e9455a;
}
/* ------------------------------------------- */
.container{
  max-width: 1180px;
  padding: 0 0.5rem;
  margin: 0 auto;
}
/* ------------------------------------------- */
#search-area{
  position: sticky;
  top:0;
  z-index: 1000;
  padding: 1rem 0.5rem;
  background: #333333;
}
#search-box{
  position: relative;
  max-width: 720px;
}
#kw-clear{
  position: absolute;
  top:8px;
  right: 100px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  border-radius: 50%;
}
#kw-clear:hover{
  background:rgba(0, 0, 0, .2);
}
#kw-clear img{
  max-width:14px;
}
#search-box input{
  width: calc(100% - 88px);
  height: 44px;
  padding:0.25rem 1rem;
  color: #757575;
  background: #f5f5cc;
  border: 2px solid #333333;
  border-right: 0;
  border-radius: 8px 0 0 8px;
}
#search-btn{
  position: relative;
  width: 84px;
  height: 44px;
  padding:0.5rem 1rem 0.5rem 2rem;
  font-weight: bold;
  color: #ffffff;
  background: #424242;
  border: 2px solid #424242;
  border-radius: 0 8px 8px 0;
}
#search-btn::before{
  position: absolute;
  top: 10px;
  left: 8px;
  width: 16px;
  height: 16px;
  content: "";
  background: url('assets/images/icon-magnifier.png') no-repeat;
  background-size: contain;
}
#search-btn:hover{
  color: #333333;
  background: #ffffff;
  transition: .5s;
}
#search-btn:hover::before{
  filter: brightness(0) saturate(100%) invert(0%) sepia(0%) saturate(6671%) hue-rotate(169deg) brightness(96%) contrast(61%);
  transition: .5s;
}
#head{
  display: flex;
}
#control-wrap{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.count-display>span{
  padding: 0 0.25rem;
}

@media (max-width:900px) {
  #head{
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    padding-bottom: 0.25rem;
  }
}

@media (max-width:600px) {
  #control-wrap{
    justify-content: center;
  }
}
.pager{
  min-height: 56px;
  margin-left: auto;
}
.pagination{
  display: flex;
  align-items: center;
}
#foot .pagination{
  justify-content: flex-end;
}
.page-item ,.pre ,.next{
  display: flex;
  align-items: center;
}
.page-item a{
  display: inline-block;
  min-width: 40px;
  height: 40px;
  padding: 0.5rem 1rem;
  margin: 0.5rem;
  color: #ffffff;
  text-align: center;
  vertical-align: bottom;
  cursor: pointer;
  background: #333333;
  border: 2px solid #333333;
  border-radius: 4px;
}
.page-item a:hover{
  color: #333333;
  background: #ffffff;
  transition: .5s;
}
.page-item.active a{
  color: #333333;
  pointer-events: none;
  background: #ffffff;
}
.pre a,.next a{
  position: relative;
  display: inline-block;
  width: 40px;
  height: 40px;
  padding: 0.5rem 1rem;
  margin: 0.5rem;
  cursor: pointer;
  background: #333333;
  border: 2px solid #333333;
  border-radius: 4px;
  transition: .5s;
}
.pre a:hover,.next a:hover{
  background: #ffffff;
  transition: .5s;
}
.pre a::before{
  position: absolute;
  top:9px;
  left: 15px;
  display: block;
  width: 20px;
  height: 20px;
  content: "";
  border-top: 3px solid #ffffff;
  border-right: 3px solid #ffffff;
  transition: .5s;
  transform: rotate(-135deg);
}
.next a::before{
  position: absolute;
  top:9px;
  right: 15px;
  display: block;
  width: 20px;
  height: 20px;
  content: "";
  border-top: 3px solid #ffffff;
  border-right: 3px solid #ffffff;
  transition: .5s;
  transform: rotate(45deg);
}
.pre a:hover::before,.next a:hover::before{
  border-color: #333333;
}
.pre.disabled a,.next.disabled a{
  color: transparent;
  background: transparent;
  border-color: transparent;
  pointer-events: none;
}
.page-item.disabled a{
  padding: 0;
  color: #333333;
  pointer-events: none;
  background: transparent;
  border-color: transparent;
}

@media (max-width:600px) {
  .pager{
    margin-left: initial;
  }
  .pagination{
    margin-left: -0.5rem;
  }
  .page-item{
    display: none;
  }
  .pre a,.next a{
    width: calc((100vw / 2) - 1rem);
  }
}

table{
  width: 100%;
}
th,td{
  padding: 4px 8px;
  border: 1px solid #333333;
}
th{
  font-weight: bold;
  color: #ffffff;
  white-space: nowrap;
  background: #333333;
}
th:nth-child(1),td:nth-child(1),
th:nth-child(2),td:nth-child(2),
th:nth-child(3),td:nth-child(3){
  text-align: center;
}
td:nth-child(1),td:nth-child(2),td:nth-child(3){
  white-space: nowrap;
}
.yomi{
  display: block;
  padding: 0.5rem;
  font-size: 0.8rem;
}
tbody tr:nth-child(even){
  background:#f2f2f2 ;
}

@media (max-width:600px) {
  thead{
    display: none;
  }
  tbody{
    border: 1px solid #333333;
  }
  tr{
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid #333333;
  }
  td{
    border-color:transparent ;
  }
  td:nth-child(1),td:nth-child(2),td:nth-child(3){
    text-align: left;
  }
  td:nth-child(1)::before{
    content: "No.";
  }
  td:nth-child(2)::before{
    content: "ã€’";
  }
  .yomi{
    display: inline-block;
  }
}
/* ------------------------------------------- */
footer{
  height: 80px;
}
#pagetop {
  position: fixed;
  right: 40px;
  bottom: 20px;
  z-index: 5500;
  display: block;
  width: 42px;
  height:42px;
  cursor: pointer;
  background: rgba(0, 0, 0, .5);
  border: 1px solid #ffffff;
  border-radius: 4px;
  opacity: 0;
  transition: .5s;
}
#pagetop.visible {
  pointer-events: auto;
  opacity: 1;
}

@media (max-width:479px) {
  #pagetop {
    bottom: 10px;
  }
}

#pagetop span {
  position: absolute;
  top: 16px;
  left: 11px;
  display: block;
  width:18px;
  height:18px;
  content:"";
  border-top:3px solid #ffffff;
  border-right:3px solid #ffffff;
  -webkit-transform: rotate(315deg);
  transform: rotate(315deg);
}
#hoge{
  width: 500px;
  height: 500px;
  background-size: contain;
}